import React from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

// require("prismjs/themes/prism-solarizedlight.css")

import './index.styl'

const TemplateWrapper = ({ children }) => (

  <div>

    <Helmet title="William Owen UK LIMETED" meta={[ { name: 'description', content: 'William Owen UK Limited, Internet Development Consultatancy and Contracting.' }, { name: 'keywords', content: 'william owen, web, development, contractor, contracting, ui, ux, design, programer, javascript, html, css, coder' }, ]} />

    <header className="site-header">

      <div className="page">

        <span className="avatar" style={{"backgroundImage": `url('https://avatars0.githubusercontent.com/u/1788835?s=460&v=4')`}} />

        <h1>

          <Link to="/">William Owen UK Limited</Link>
          <span className="sub-title">Internet Development Consultatancy and Contracting</span>

        </h1>

        <div className="availability">

          <Link to="/contact/">Currently available for project work</Link>

        </div>

      </div>

      <nav>

        <div className="page">

          <Link to="/">Home</Link>
          <Link to="/about/">About</Link>
          <Link to="/articles/">Articles</Link>
          <Link to="/projects">Projects</Link>
          <Link to="/tech">Tech Stack</Link>
          <Link to="/contact/">Contact</Link>

        </div>

      </nav>

    </header>

    <div>

      { children() }

    </div>

    <footer className="site-footer">

      <div className="page">

        <nav>

          <a href="https://twitter.com/twilowen">Twitter: @twilowen</a>
          <a href="https://github.com/William-Owen">GitHub: William-Owen</a>
          <a href="www.linkedin.com/in/williamowenuk">LinkedIn</a>

        </nav>

        <div className="contact">

          <p>You can me contact on <a href="tel:+442070978899">+44 (0) 207 097 8899</a></p>

        </div>

        <div className="pride">

          <h4>
            This wabsite was hand crafted, without the use of existing templates, using some of these awesome techknologies and services.
          </h4>

          <a href="https://www.gatsbyjs.org/">

            <img src="data:image/svg+xml;base64," />

            <p>Built with Gatsby</p>

          </a>

          <a href="https://reactjs.org/">

            <img src="data:image/svg+xml;base64," />

            <p>Built using React.js</p>

          </a>

          <a href="http://stylus-lang.com/">

            <img src="data:image/svg+xml;base64," />

            <p>Styled using Stylus</p>

          </a>

          <a href="https://github.com/William-Owen/">

            <img src="data:image/svg+xml;base64," />

            <p>Hosted on GitHub</p>

          </a>

        </div>

        <p className="company-details">William Owen UK LIMITED registered in England no. 08243425. VAT number 152978378</p>
        <p className="copyright">&copy; 2017 William Owen UK LIMITED</p>

      </div>

    </footer>

  </div>

)

TemplateWrapper.propTypes = {

  children: PropTypes.func,

}

export default TemplateWrapper


